import React, { useEffect, useState } from 'react';
import { useDropzone } from 'react-dropzone';
import styled from '@emotion/styled';
import { Box, Button, CircularProgress } from '@mui/material';
import FolderSharedIcon from '@mui/icons-material/FolderShared';
import { UploadFile } from './Helper';
import { useNavigate } from "react-router-dom";
import Modal from '@mui/material/Modal';
import Typography from '@mui/material/Typography';
const getColor = (props) => {
  if (props.isDragAccept) {
    return '#00e676';
  }
  if (props.isDragReject) {
    return '#ff1744';
  }
  if (props.isFocused) {
    return '#2196f3';
  }
  return '#eeeeee';
}
const Container = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  border-width: 2px;
  border-radius: 2px;
  border-color: ${props => getColor(props)};
  border-style: dashed;
  background-color: #fafafa;
  color: #bdbdbd;
  outline: none;
  transition: border .24s ease-in-out;
`;
const steps = ['Select Files', 'Tag Files'];
function DropzoneTest() {
  const [token, setToken] = useState('');
  const [tag, setTag] = useState('Vehicle Photos');
  const [toggle, setToggle] = useState(false);
  const [status, setStatus] = useState(false);
  const [customer, setCustomer] = useState({
    name: 'Kunal',
    id: '4513699',
    vehicle: {
      id: '961543',
      name: 'Honda City'
    }
  })
  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    textAlign: 'center',
    borderRadius: '10px',
    p: 4,
  };
  const {
    acceptedFiles,
    fileRejections,
    getRootProps,
    getInputProps,
    isFocused,
    isDragAccept,
    isDragReject,
  } = useDropzone({
    accept: {
      'image/jpg': ['.jpg'],
      'image/png': ['.png'],
      'image/tiff': ['.tif'],
      'application/pdf': ['.pdf']
    }
  });
  const navigate = useNavigate();
  const acceptedFileItems = acceptedFiles.map(file => (
    <li key={file.path}>
      {file.path} - {file.size} bytes
    </li>
  ));

  const fileRejectionItems = fileRejections.map(({ file, errors }) => (
    <li key={file.path}>
      {file.path} - {file.size} bytes
      <ul>
        {errors.map(e => (
          <li key={e.code}>{e.message}</li>
        ))}
      </ul>
    </li>
  ));
  const [activeStep, setActiveStep] = React.useState(0);
  const [completed, setCompleted] = React.useState({});

  const totalSteps = () => {
    return steps.length;
  };

  const completedSteps = () => {
    return Object.keys(completed).length;
  };

  const isLastStep = () => {
    return activeStep === totalSteps() - 1;
  };

  const allStepsCompleted = () => {
    return completedSteps() === totalSteps();
  };

  const handleNext = () => {
    if (activeStep === 0) {
      if (acceptedFiles.length === 0) {
        alert("Please select files to upload")
      } else {
        const newActiveStep =
          isLastStep() && !allStepsCompleted()
            ?
            steps.findIndex((step, i) => !(i in completed))
            : activeStep + 1;
        setActiveStep(newActiveStep);
      }
    } else {
      const newActiveStep =
        isLastStep() && !allStepsCompleted()
          ?
          steps.findIndex((step, i) => !(i in completed))
          : activeStep + 1;
      setActiveStep(newActiveStep);
    }
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };
  const handleReset = () => {
    setActiveStep(0);
    setCompleted({});
  };
  const upload = (e) => {
    e.preventDefault();
    setToggle(true);
    var formData = new FormData();
    formData.append("tag", tag)
    acceptedFiles.map((item, index) => {
      formData.append(item.name, item)
      if (index === acceptedFiles.length - 1) {
        UploadFile(formData, customer).then((data) => {
          if (data.success) {
            setToggle(false);
            setStatus(true)
            alert("Files uploaded successfully")
            handleReset();
          }
        }).catch((err) => {
          setToggle(false);
          console.log(err)
        })
      }
    })
  }
  useEffect(() => {
    if (acceptedFiles.length !== 0) {
      console.log(acceptedFiles);
    }
  }, [acceptedFiles])
  const redirect = (e) => {
    e.preventDefault();
    navigate("/files")
  }
  return (
    <div className="container p-5">
      <div className="row mb-2">
        <div className="col">
          <Button className="float-end" variant="contained" onClick={e => redirect(e)} endIcon={<FolderSharedIcon />}>Vehicle Files</Button>
        </div>
      </div>
      <Modal
        open={toggle}
        onClose={() => setToggle(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <CircularProgress color="success" />
          <h4 className="test-secondary">Uploading</h4>
        </Box>
      </Modal>
      <br />
      <br />
      <br />
      <Box sx={{ width: '100%' }}>
        <div className='p-5'>
          {allStepsCompleted() ? (
            <React.Fragment>
              <Typography sx={{ mt: 2, mb: 1 }}>
                All steps completed - you&apos;re finished
              </Typography>
              <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
                <Box sx={{ flex: '1 1 auto' }} />
                <Button onClick={handleReset}>Reset</Button>
              </Box>
            </React.Fragment>
          ) : (
            <React.Fragment>
              {
                activeStep === 0 ?
                  <div className="p-4">
                    <Container {...getRootProps({ isFocused, isDragAccept, isDragReject })}>
                      <input {...getInputProps()} />
                      <p>Drag 'n' drop some files here, or click to select files</p>
                    </Container>
                    <aside>
                      {acceptedFileItems.length > 0 ?
                        <div className="pt-4">
                          <h6 className="text-success">Accepted files</h6>
                          <ul>{acceptedFileItems}</ul>
                        </div>
                        : ''}
                      {fileRejectionItems.length > 0 ?
                        <div className="pt-4">
                          <h6 className="text-danger">Rejected files</h6>
                          <ul>{fileRejectionItems}</ul>
                        </div>
                        : ''}
                    </aside>
                  </div> : ''}
              {activeStep === 1 ?
                <div>
                  <div className="mb-3 p-4 text-center" style={{ display: acceptedFiles.length > 0 ? 'block' : 'none' }}>
                    <div className='ps-1'>
                      <input type="radio" class="btn-check" name="options-outlined" value={"Vehicle Photos"} id="btn1" autocomplete="off" checked />
                      <label class="btn btn-outline-success" for="btn1">Vehicle Photos</label>&nbsp;&nbsp;
                      <input type="radio" class="btn-check" name="options-outlined" value={"Vehicle Service Documents"} id="btn2" autocomplete="off" />
                      <label class="btn btn-outline-success" for="btn2">Vehicle Service Documents</label>
                    </div>
                  </div>
                  <div className="p-4" style={{ display: acceptedFiles.length === 0 ? 'block' : 'none' }}>
                    <h5 className='msg text-center'>No files selected !</h5>
                  </div>
                </div>
                : ''
              }
              <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
                {activeStep !== 0 ? <Button
                  onClick={handleBack}
                  variant='outlined'
                  sx={{ mr: 1 }}
                >
                  Back
                </Button> : ''}
                <Box sx={{ flex: '1 1 auto' }} />
                {activeStep !== 1 ?
                  <Button variant='contained' onClick={handleNext} sx={{ mr: 1 }}>
                    Next
                  </Button>
                  :
                  <Button variant='contained' onClick={upload} sx={{ mr: 1 }}>
                    Upload
                  </Button>
                }

              </Box>
            </React.Fragment>
          )}
        </div>
      </Box>
    </div>
  );
}
export default DropzoneTest